<template>
  <div id="login">
      <div class="title">
          WELCOME BLOG!
      </div>
      <div class="loginBox">
          <p>Login</p>
          <label>
              <span><i class="iconfont icon-yonghu"></i></span>
              <input type="text" class="username" v-model="user.username"/>
          </label>
          <label>
              <span><i class="iconfont icon-mima"></i></span>
              <input type="password" class="password" v-model="user.password"/>
          </label>
          <button type="button" @click="login">登&nbsp;&nbsp;&nbsp;&nbsp;录</button>
          <div class="tipPanel">
              <span>忘记密码？</span>
              <span @click="toRegister">注册</span>
          </div>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'login',
  data () {
    return {
        user:{
            username:'',
            password:''
        }
    }
  },
  created:function(){
    //   axios({
    //       mathod
    //   })
  },
  methods:{
      toRegister:function(){
          this.$router.replace('/register');
      },
      login:function(user){
        // axios({
        //     mathod:'post',
        //     baseURL:'/api',
        //     url:'ajax/loginReg'
        // }).then(function(res){
        //     console.log(res);
        // })
        this.$http.post('/ajax/loginReg')
            .then(res=>{
            console.log(res)
        })
      }
  }
}
</script>
<style scoped>
    #login{
        position:relative;
        width:100%;
        height:100%;
        background-image: url(../assets/bg.jpg);
        background-size: 100% 100%; 
        opacity: 0.8;
    }
    #login>.title{
        position: absolute;
        top: 25%;
        left: 30%;
        font-family: 'yahei';
        color: #ccc;
        font-size: 30px;
    }
    #login>.loginBox{
        position: absolute;
        width: 500px;
        height: 280px;
        top: 50%;
        left: 50%;
        margin-left: -250px;
        margin-top: -140px;
        color: #fff;
        text-align: center;
        background: #333;
        box-shadow: 5px 5px 25px #000, -5px -5px 25px #000;
        box-sizing: border-box;
        padding-top: 40px;
    }
    #login>.loginBox>p{
        width:100%;
        line-height: 30px;
        font-size:24px;
        text-align: center;
        font-family: 'yahei';
    }
    #login>.loginBox label{
        position:relative;
        display: block;
        color:#ccc;
        line-height: 50px;
    }
    #login>.loginBox label span{
        position: absolute;
        top: 12px;
        left: 127px;
        display: inline-block;
        line-height: 24px;
        color: #333;
        }
     #login>.loginBox label i{
        font-size: 20px;
    }
    #login>.loginBox input{
        line-height: 24px;
        padding: 0 25px;
        outline: 0;
        width: 40%;
        border: 1px solid #ccc;
        font-size:16px;
        border-radius: 4px;
    }
    #login>.loginBox button{
        width: 50%;
        line-height: 28px;
        margin-top: 20px;
        border: 0;
        background: #2ba245;
        color: #fff;
        border-radius: 4px;
        cursor: pointer;
        outline:0;
    }
    #login>.loginBox .tipPanel{
        width:48%;
        margin:0 auto;
    }
    #login>.loginBox .tipPanel>span{
        font-size:12px;
        cursor:pointer;
        margin-top: 10px;
        font-weight:normal;
    }
    #login>.loginBox .tipPanel>span:hover{
        color:#2ba245;
    }
    #login>.loginBox .tipPanel>span:first-child{
        float:left;
    }
    #login>.loginBox .tipPanel>span:last-child{
        float:right;
    }
</style>


