<template>
  <div class="Index">
    <Header></Header>
    <div class="main">

    </div>
  </div>
</template>

<script>
import Header from './Header'
export default {
  name: 'Index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  created:function(){
    if(window.sessionStorage.getItem("username")==null){
      this.$router.replace('/login');
    }
  },
  components:{
    Header
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .Index{
    position:relative;
    width:100%;
  }
  .main{
    height:2000px;
    width:80%;
    margin:0 auto;
  }
</style>



